.App {
  text-align: center;
  /*width: 100%;*/
}

.App-header {
  /*width: 100%;*/
  background-color: #282c34;
  /*height: 150px;*/
  height: auto;
  color: white;
  display: grid;
  grid-template-rows: 1fr max-content;
  align-items: center;
  /*justify-content: space-between;*/
  justify-items: center;
  /*display: flex;*/
  /*flex-direction: row;*/
  /*align-items: center;*/
  /*justify-content: center;*/
  margin-bottom: 0.5rem;
}

.App-header .title {
  font-size: calc(10px + 2vmin);
}
.App-header .controls {
  font-size: calc(8px + 1.1vmin);
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(8rem, 1fr) );
  grid-gap: 0.2rem;
  grid-template-rows: 1fr 1fr;
  margin: 0 0.5rem;
  align-items: center;
}

.App-header .controls > div input {
  width: 90%;
}

.update-button {
  /*grid-column: 1;*/
}

.update-button button {
  /*width: 90%;*/
  /*height: 1.5rem;*/
  padding-left: 0.5rem;
  font-size: 0.7rem;
  border-radius: 1rem;
  border-width: 0;
  /*grid-column: 3;*/
  /*display: inline-flex;*/
  /*align-items: center;*/
}
.update-button button:disabled {
  /*width: 70%;*/
  /*height: 70%;*/
  font-size: 0.5rem;
  border-radius: 0.5rem;
  /*padding: 1rem 0;*/
  opacity: 0.5;
}

.upper-left-part {
  position: absolute;
  display: flex;
  align-items: flex-start;
  left: 0rem;
  top: 0rem;
  width: 100%;
}

.loading-icon {
  padding: 0.5rem;
}

.App-link {
  color: #61dafb;
}

body {
  background-color: #282c34;
}
.github-logo {
  cursor: pointer;
  position: absolute;
  display: flex;
  align-items: center;
  right: 0rem;
  top: 0rem;
  padding: 0.5rem;
}

.github-logo:hover {
  border-radius: 0.25rem;
  background-color: #464646;
}

a, a:hover, a:focus, a:active {
  text-decoration: none;
  color: white;
}

.github-logo a {
  display: flex;
}

.github-logo:hover .github-link-text{
  display: block;
}

.github-link-text{
  display: none;
  padding-right: 0.5rem;
  font-size: 0.75rem;
}

.github-logo img {
  /*padding: 0.5rem;*/
  width: 1.25rem;
  /*background-color: transparent;*/
}

.hide {
  visibility: hidden;
}

.show {
  visibility: visible;
}

.sketch_wrapper {
  display: flex;
  flex-grow: 1;
  justify-content: center;
  align-content: center;
}


.sketch_wrapper > * {
  border: 1px solid black;
}


